      abstract:
        "Metal guitarist.\r\rBorn September 13th, 1961, in La Mesa, California.\r\rFormer Metallica member, he was fired from the band in 1983 because of drug and alcohol problems that often resulted in violence against other band members. He would suffer from heroin addiction until 1995 when he was finally able to quit the drugs completely.\r\rMustaine formed Megadeth in 1984 with bassist David Ellefson, fellow guitarist Kerry King, and drummer Lee Rash. The band has gone through several lineup changes, but Mustaine has always remained as bandleader.\r\rIn 2002, Megadeth disbanded as a result of nerve damage (radial neuropathy) to Mustaine's left arm. He has since recovered feeling in his arm, and the band has reformed and begun to record and tour again.\r\rDave married Pam Casselberry in 1991, and they had two children together (a son, [url=http://www.discogs.com/artist/Justis+Mustaine]Justis David Mustaine[/url], born in 1992, and a daughter, Electra Nicole, born in 1998). He filed for divorce in 2006.\r",
