          lyrics:
            "By the last breath, the fourth winds blow<br>Better raise your ears<br>The sound of hooves knock at your door<br>Lock up your wife and children, now<br>It&apos;s time to wield the blade<br>For now you&apos;ve got some company<br><br>The Horsemen are drawing nearer<br>On leather steeds, they ride<br>They&apos;ve come to take your life<br>On through the dead of night<br>With the Four Horsemen, ride<br>Or choose your fate and die<br>Oh yeah yeah<br><br>You have been dying since the day you were born<br>You know it&apos;s all been planned<br>The quartet of deliverance rides<br>A sinner once, a sinner twice<br>No need for confessions now<br>&apos;Cause now you&apos;ve got the fight of your life<br><br>The Horsemen are drawing nearer<br>On leather steeds, they ride<br>They&apos;ve come to take your life<br>On through the dead of night<br>With the Four Horsemen, ride<br>Or choose your fate and die<br>Oh yeah yeah<br><br>Time has taken its toll on you<br>The lines that crack your face<br>Famine, your body, it has torn through<br>Withered in every place<br>Pestilence, for what you had to endure<br>And what you have put others through<br>Death, deliverance, for you, for sure<br>Now there&apos;s nothing you can do<br><br>So, gather &apos;round, young warriors, now<br>And saddle up your steeds<br>Killing scores with demon swords<br>Now is the death of doers of wrong<br>Swing the judgement hammer down<br>Safely inside armor, blood, guts, and sweat<br><br>The Horsemen are drawing nearer<br>On leather steeds they ride<br>They&apos;ve come to take your life<br>On through the dead of night<br>With the Four Horsemen ride<br>Or choose your fate and die<br>Oh yeah yeah",
