          urlSong: "http://lyrics.wikia.com/Metallica:Jump_In_The_Fire",
          lyrics:
            "Down in the depths of my fiery home, the summons bell will chime<br>Tempting you and all the earth to join our sinful kind<br>There is a job to be done and I&apos;m the one you people make me do it<br>Now it is time for your fate and I won&apos;t hesitate to pull you down into this pit<br><br>So come on<br>Jump in the fire<br>So come on<br>Jump in the fire<br><br>With hell in my eyes and with death in my veins, the end is closing in<br>Feeding on the minds of man and from their souls within<br>My disciples all shout to search you out and they always shall obey<br>Follow me now my child, not the meek or the mild but do just as I say<br><br>So come on<br>Jump in the fire<br>So come on<br>Jump in the fire<br><br>Jump by your will or be taken by force, I&apos;ll get you either way<br>Trying to keep the hellfire lit, I am stalking you as prey<br>Living your life as me, I am you you see, there is part of me in everyone<br>So reach down, grab my hand, walk with me through the land, come home where you belong<br><br>So come on<br>Jump in the fire<br>So come on<br>Jump in the fire<br><br>So come on<br>Jump in the fire<br>So come on<br>Jump in the fire<br>C&apos;mon, jump baby, now",
          urlWikipedia: "http://en.wikipedia.org/wiki/Jump_in_the_Fire",
          id_album: "5714debe25ac0d8aee36b662",
          format: ["Compact Cassette", "Gramophone record"],
          genre: ["Thrash metal"],
          producer: ["Jon Zazula"],
          recordLabel: ["Megaforce Records"],
          writer: ["Dave Mustaine", "Lars Ulrich", "James Hetfield"],
          recorded: ["--05-10"],
          abstract:
            '"Jump in the Fire" is a song by American heavy metal band Metallica. It was released as the second and final single from their debut album, Kill \'Em All. The single was accompanied by fake live performances of "Phantom Lord" and "Seek & Destroy" which were alternate studio recordings with sounds of a crowd overdubbed in.Alongside "Hit the Lights" and "No Remorse", "Jump in the Fire" is one of Metallica\'s first original songs, having been included on Ron McGovney\'s \'82 Garage demo, an unreleased recording. The original lyrics and content, which dealt with sex, were written by Dave Mustaine in his former band "Panic" at the age of 16. The original version that Mustaine introduced to Hetfield and Ulrich upon joining Metallica was raw. The three worked together on refining the song and the final outcome is what is heard on the demo. However, much like the events surrounding "The Four Horsemen", new lyrics were written by James Hetfield upon Mustaine\'s departure from Metallica. The new lyrics revolve around people being damned to Hell and therefore "jumping in the fire." Lars Ulrich claims that they had written the song to sound like "Run to the Hills" by Iron Maiden, which was popular at the time. Current live performances since 2004 are in D standard tuning, as opposed to the E standard tuning of earlier live performances.',
          releaseDate: ["1984-01-20"],
          runtime: ["280.0"],
          award: [],
