          urlSong: "http://lyrics.wikia.com/Metallica:Whiplash",
          lyrics:
            "Late at night, all systems go, you&apos;ve come to see the show<br>We do our best, you&apos;re the rest, you make it real, you know<br>There&apos;s a feeling deep inside that drives you fuckin&apos; mad<br>A feeling of a hammerhead, you need it oh so bad<br><br>Adrenalin starts to flow<br>You&apos;re thrashing all around<br>Acting like a maniac<br>Whiplash!<br><br>Bang your head against the stage like you never did before<br>Make it ring, make it bleed, make it really sore<br>In a frenzied madness with your leather and your spikes<br>Heads are bobbing all around, it&apos;s hot as hell tonight<br><br>Adrenalin starts to flow<br>You&apos;re thrashing all around<br>Acting like a maniac<br>Whiplash!<br><br>Here on the stage the Marshalls&apos; noise is piercing through your ears<br>It kicks your ass, kick your face, exploding feeling nears<br>Now&apos;s the time to let it rip, to let it fuckin&apos; loose<br>We&apos;re gathered here to maim and kill, &apos;cause this is what we choose<br><br>Adrenalin starts to flow<br>You&apos;re thrashing all around<br>Acting like a maniac<br><br>Here we go!<br><br>Whiplash!<br><br>The show is through, the metal&apos;s gone, it&apos;s time to hit the road<br>Another town, another gig, again we will explode<br>Hotel rooms and motorways, life out here is raw<br>But we&apos;ll never stop, we&apos;ll never quit, &apos;cause we&apos;re Metallica<br><br>Adrenalin starts to flow<br>You&apos;re thrashing all around<br>Acting like a maniac",
