          lyrics:
            "No mercy for what we are doing<br>No thought to even what we have done<br>We don&apos;t need to feel the sorrow<br>No remorse for the helpless one<br><br>War without end<br><br>No remorse, no repent<br>We don&apos;t care what it meant<br>Another day, another death<br>Another sorrow, another breath<br>No remorse, no repent<br>We don&apos;t care what it meant<br>Another day, another death<br>Another sorrow, another breath<br><br>Blood feeds the war machine<br>As it eats its way across the land<br>We don&apos;t need the feel the sorrow<br>No remorse is the one command<br><br>War without end<br><br>No remorse, no repent<br>We don&apos;t care what it meant<br>Another day, another death<br>Another sorrow, another breath<br>No remorse, no repent<br>We don&apos;t care what it meant<br>Another day, another death<br>Another sorrow, another breath<br><br>Only the strong survive<br>No one to save the weaker race<br>We are ready to kill all comers<br>Like a loaded gun right at your face<br><br>War without end<br>No remorse, no repent<br>We don&apos;t care what it meant<br>Another day, another death<br>Another sorrow, another breath<br>No remorse, no repent<br>We don&apos;t care what it meant<br>Another day, another death<br>Another sorrow, another breath<br><br>Attack<br>Bullets are flying<br>People are dying<br>With madness surrounding<br>All hell&apos;s breaking loose<br>Soldiers are hounding<br>Bodies are mounting<br>Cannons are shouting to take their abuse<br>With war machines going<br>Blood starts a-flowing<br>No mercy given to anyone here<br>The furious fighting<br>Swords are like lightning<br>It all becomes frightening<br>You know death is near<br>No remorse",
