            "Guilty as charged but damn it, it ain't right<br>There's someone else controlling me<br>Death in the air, strapped in the electric chair<br>This can't be happening to me<br><br>Who made you God to say<br>\"I'll take your life from you\"?<br><br>Flash before my eyes<br>Now it's time to die<br>Burning in my brain<br>I can feel the flame<br><br>Wait for the sign to flick the switch of death<br>It's the beginning of the end<br>Sweat chilling cold as I watch death unfold<br>Consciousness my only friend<br><br>My fingers grip with fear<br>What am I doing here?<br><br>Flash before my eyes<br>Now it's time to die<br>Burning in my brain<br>I can feel the flame<br><br>Someone help me, oh please God help me<br>They're trying to take it all away<br>I don't want to die<br><br>Someone help me, oh please God help me<br>They're trying to take it all away<br>I don't want to die<br><br>Time moving slow, the minutes seem like hours<br>The final curtain all I see<br>How true is this? Just get it over with<br>If this is true, just let it be<br><br>Wakened by horrid scream<br>Freed from this frightening dream<br><br>Flash before my eyes<br>Now it's time to die<br>Burning in my brain<br>I can feel the flame",
