          urlSong: "http://lyrics.wikia.com/Metallica:For_Whom_The_Bell_Tolls",
          lyrics:
            "Make his fight on the hill in the early day<br>Constant chill deep inside<br>Shouting gun, on they run through the endless grey<br>On they fight, for the right, yes, but who&apos;s to say?<br>For a hill, men would kill, why? They do not know<br>Stiffened wounds test their pride<br>Men of five, still alive through the raging glow<br>Gone insane from the pain that they surely know<br><br>For whom the bell tolls<br>Time marches on<br>For whom the bell tolls<br><br>Take a look to the sky just before you die<br>It&apos;s the last time he will<br>Blackened roar, massive roar, fills the crumbling sky<br>Shattered goal fills his soul with a ruthless cry<br>Stranger now, are his eyes, to this mystery<br>He hears the silence so loud<br>Crack of dawn, all is gone except the will to be<br>Now they see, what will be, blinded eyes to see<br><br>For whom the bell tolls<br>Time marches on<br>For whom the bell tolls",
          urlWikipedia:
            "http://en.wikipedia.org/wiki/For_Whom_the_Bell_Tolls_(Metallica_song)",
          id_album: "5714debe25ac0d8aee36b663",
          format: [],
          genre: ["Heavy metal music"],
          producer: ["Metallica", "Flemming Rasmussen"],
          recordLabel: ["Elektra Records"],
          writer: ["James Hetfield", "Lars Ulrich", "Cliff Burton"],
          recorded: ["--02-20"],
          abstract:
            '"For Whom the Bell Tolls" is a song by American thrash metal band Metallica. It was released as the second promotional single from their second album, Ride the Lightning.  Among their most-played songs, it has, as of November 16, 2014, been performed 1,305 times, behind only "One" (1,324), "Seek & Destroy" (1,396), "Creeping Death" (1,413) and "Master of Puppets" (1,467).The song was inspired by Ernest Hemingway\'s 1940 novel of the same name about the dishonor of modern warfare and the protagonist\'s imminent doom during the bloody Spanish Civil War. Specific allusions are made to the scene in which five soldiers are obliterated during an air-strike, after taking a position on a hill."For Whom the Bell Tolls" was released as a promo single with two versions of the song, an edited version on side A and the album version on the b-side.The chromatic introduction, which is often mistaken for an electric guitar, is in fact Cliff Burton playing his bass guitar through distortion and wah-wah.  The intro was written by Burton before joining Metallica. Burton first played it in 1979 in a 12-minute jam at a battle of the bands with his second band Agents of Misfortune (with his old bandmate from EZ-Street and future Faith No More guitarist "Big" Jim Martin).The guitars and bass in the song are tuned slightly sharper than standard on this performance (and sharper than the other tracks on the album). Rumors and speculation abound regarding the reason for the discrepancy, but no definitive explanation has surfaced - one reasoning is the slightly sharpened guitar tuning is used to keep the guitars in line with the song\'s intro bell chimes.',
