          lyrics:
            "Life it seems will fade away<br>Drifting further every day<br>Getting lost within myself<br>Nothing matters, no one else<br><br>I have lost the will to live<br>Simply nothing more to give<br>There is nothing more for me<br>Need the end to set me free<br><br>Things not what they used to be<br>Missing one inside of me<br>Deathly loss, this can&apos;t be real<br>Cannot stand this hell I feel<br><br>Emptiness is filling me<br>To the point of agony<br>Growing darkness taking dawn<br>I was me but now he&apos;s gone<br><br>No one but me can save myself but it&apos;s too late<br>Now I can&apos;t think, think why I should even try<br><br>Yesterday seems as though it never existed<br>Death greets me warm, now I will just say goodbye<br>Goodbye",
