          urlSong: "http://lyrics.wikia.com/Metallica:Trapped_Under_Ice",
          lyrics:
            "I don&apos;t know how to live through this hell<br>Woken up, I&apos;m still locked in this shell<br>Frozen soul, frozen down to the core<br>Break the ice, I can&apos;t take anymore<br><br>Freezing, can&apos;t move at all<br>Screaming, can&apos;t hear my call<br>I am dying to live<br>Cry out, &quot;I&apos;m trapped under ice&quot;<br><br>Crystallized, as I lay here and rest<br>Eyes of glass stare directly at death<br>From deep sleep I have broken away<br>No one knows, no one hears what I say<br><br>Freezing, can&apos;t move at all<br>Screaming, can&apos;t hear my call<br>I am dying to live<br>Cry out, &quot;I&apos;m trapped under ice&quot;<br><br>Scream from my soul<br>Fate mystified<br>Hell forever more<br>Scream from my soul<br>Fate mystified<br>Hell forever more<br><br>No release from my cryonic state<br>What is this? I&apos;ve been stricken by fate<br>Wrapped up tight, cannot move, can&apos;t break free<br>Hand of doom has a tight grip on me<br><br>Freezing, can&apos;t move at all<br>Screaming, can&apos;t hear my call<br>I am dying to live<br>Cry out, &quot;I&apos;m trapped under ice&quot;<br>Freezing, can&apos;t move at all<br>Screaming, can&apos;t hear my call<br>I am dying to live<br>Cry out, &quot;I&apos;m trapped under ice&quot;",
