          lyrics:
            "Slaves, Hebrews born to serve to the Pharaoh<br>Heed to his every word, live in fear<br>Faith of the unknown one, the deliverer<br>Wait, something must be done for hundred years<br><br>So let it be written<br>So let it be done<br>I&apos;m sent here by the chosen one<br>So let it be written<br>So let it be done<br>To kill the first born Pharaoh son<br>I&apos;m creeping death<br><br>Now, let my people go, land of Goshen<br>Go, I will be with thee, bush of fire<br>Blood, running red and strong, down the Nile<br>Plague, darkness three days long, hail to fire<br><br>So let it be written<br>So let it be done<br>I&apos;m sent here by the chosen one<br>So let it be written<br>So let it be done<br>To kill the first born Pharaoh son<br>I&apos;m creeping death<br><br>Die by my hand<br>I creep across the land<br>Killing first-born man<br>Die by my hand<br>I creep across the land<br>Killing first-born man<br><br>I rule the midnight air, the destroyer<br>Born, I shall soon be there, deadly mass<br>I creep the steps and floor, final darkness<br>Blood, lambs blood painted door, I shall pass<br><br>So let it be written<br>So let it be done<br>I&apos;m sent here by the chosen one<br>So let it be written<br>So let it be done<br>To kill the first born Pharaoh son<br>I&apos;m creeping death",
