          abstract:
            '"Creeping Death" is a song by the American thrash metal band Metallica, released as the lead and only single from their second studio album, Ride the Lightning (1984). Written from the perspective of the Angel of Death, it describes the Plague of the Death of the Firstborn (Exodus 12:29). One of Metallica\'s most frequently performed songs, it has been played live 1,413 times (second only to "Master of Puppets", at 1,467), it has occasionally been used on various tours as the opening song of the band\'s set. It stands as a classic example of the band\'s thrash style, albeit slower than the material on their first album, Kill \'Em All. The song\'s middle section, with its ominous chants of "Die!" set to a phrygian mode chord progression, is a fan participation staple during Metallica shows.The single was released through Music for Nations in the UK and France. The b-sides were the cover songs "Am I Evil?" (originally by Diamond Head) and "Blitzkrieg" (originally by Blitzkrieg). Together these covers were known as Garage Days Revisited, which set the stage for Metallica\'s next cover album, The $5.98 E.P.: Garage Days Re-Revisited EP.The artwork was done by Alvin Petty. The logo and the song\'s title were added with a plastic layover to the existing artwork. Kirk Hammett had seen the picture hanging up at Petty\'s house and remarked that it would be perfect for the single and picture-disk that were about to be finished.',
