          lyrics:
            "Messenger of fear in sight<br>Dark deception kills the light<br><br>Hybrid children watch the sea<br>Pray for father, roaming free<br><br>Fearless wretch, insanity<br>He watches, lurking beneath the sea<br>Great old one, forbidden site<br>He searches, hunter of the shadows is rising<br><br>Immortal<br>In madness you dwell<br><br>Crawling chaos underground<br>Cult has summoned twisted sound<br><br>Out from ruins once possessed<br>Fallen city, living death<br><br>Fearless wretch, insanity<br>He watches, lurking beneath the sea<br>Timeless sleep has been upset<br>He awakens, hunter of the shadows is rising<br><br>Immortal<br>In madness you dwell<br><br>In madness you dwell<br><br>Not dead which eternal lie<br>Stranger eons, death may die<br><br>Drain you of your sanity<br>Face the thing that should not be<br><br>Fearless wretch, insanity<br>He watches, lurking beneath the sea<br>Great old one, forbidden site<br>He searches, hunter of the shadows is rising<br><br>Immortal<br>In madness you dwell",
