          lyrics:
            "Bodies fill the fields I see, hungry heroes end<br>No one to play soldier now, no one to pretend<br>Running blind through killing fields, bred to kill them all<br>Victim of what said should be, a servant till I fall<br><br>Soldier boy, made of clay, now an empty shell<br>Twenty-one, only son but he served us well<br>Bred to kill, not to care, do just as we say<br>Finished here, greetings Death, he&apos;s yours to take away<br><br>Back to the front, you will do what I say, when I say<br>Back to the front, you will die when I say, you must die<br>Back to the front, you coward, you servant, you blind man<br><br>Barking of machine-gun fire does nothing to me now<br>Sounding of the clock that ticks, get used to it somehow<br>More a man, more stripes you wear, glory-seeker trends<br>Bodies fill the fields I see, the slaughter never ends<br><br>Soldier boy, made of clay, now an empty shell<br>Twenty-one, only son but he served us well<br>Bred to kill, not to care, do just as we say<br>Finished here, greetings Death, he&apos;s yours to take away<br><br>Back to the front, you will do what I say, when I say<br>Back to the front, you will die when I say, you must die<br>Back to the front, you coward, you servant, you blind man<br><br>Why am I dying?<br>Kill, have no fear<br>Lie, live off lying<br>Hell, hell is here<br><br>Why am I dying?<br>Kill, have no fear<br>Lie, live off lying<br>Hell, hell is here<br>I was born for dying<br><br>Life planned out before my birth, nothing could I say<br>Had no chance to see myself, moulded day by day<br>Looking back I realize, nothing have I done<br>Left to die with only friend, alone I clench my gun<br><br>Soldier boy, made of clay, now an empty shell<br>Twenty-one, only son but he served us well<br>Bred to kill, not to care, do just as we say<br>Finished here, greetings Death, he&apos;s yours to take away<br><br>Back to the front, you will do what I say, when I say<br>Back to the front, you will die when I say, you must die<br>Back to the front, you coward, you servant, you blind man, back to the front<br><br>Back to the front<br>Back to the front<br>Back to the front<br>Back to the front",
          urlWikipedia: "http://en.wikipedia.org/wiki/Disposable_Heroes",
