          lyrics:
            "Dealing out the agony within<br>Charging hard and no one&apos;s going to give in<br>Living on your knees, conformity<br>Or dying on your feet for honesty<br><br>Inbred, our bodies work as one<br>Bloody, but never cry submission<br>Following our instinct, not a trend<br>Go against the grain until the end<br><br>Blood will follow blood<br>Dying time is here<br>Damage, Incorporated<br><br>Slamming through, don&apos;t fuck with razorback<br>Stepping out? You&apos;ll feel our hell on your back<br>Blood follows blood and we make sure<br>Life ain&apos;t for you and we&apos;re the cure<br><br>Honesty is my only excuse<br>Try to rob us of it, but it&apos;s no use<br>Steamroller action crushing all<br>Victim is your name and you shall fall<br><br>Blood will follow blood<br>Dying time is here<br>Damage, Incorporated<br><br>We chew and spit you out<br>We laugh, you scream and shout<br>All flee, with fear you run<br>You&apos;ll know just where we come from<br>Damage, Incorporated<br>Go<br><br>Damage jackals ripping right through you<br>Sight and smell of this, it gets me going<br>Know just how to get just what we want<br>Tear it from your soul in nightly hunt<br><br>Fuck it all and fucking no regrets<br>Never happy endings on these dark sets<br>All&apos;s fair for Damage, Inc. you see<br>Step a little closer, if you please<br><br>Blood will follow blood<br>Dying time is here<br>Damage, Incorporated",
