          urlSong: "http://lyrics.wikia.com/Metallica:Eye_Of_The_Beholder",
          lyrics:
            "Do you see what I see?<br>Truth is an offense<br>Your silence for your confidence<br>Do you hear what I hear?<br>Doors are slamming shut<br>Limit your imagination, keep you where they must<br><br>Do you feel what I feel?<br>Bittering distress<br>Who decides what you express?<br>Do you take what I take?<br>Endurance is the word<br>Moving back instead of forward seems to me absurd<br><br>Doesn&apos;t matter what you see<br>Or into it what you read<br>You can do it your own way<br>If it&apos;s done just how I say<br><br>Independence limited<br>Freedom of choice is made for you my friend<br>Freedom of speech is words that they will bend<br>Freedom with their exception<br><br>Do you fear what I fear?<br>Living properly<br>Truths to you are lies to me<br>Do you choose what I choose?<br>More alternatives<br>Energy derives from both the plus and negative<br><br>Do you need what I need?<br>Boundaries overthrown<br>Look inside, to each his own<br>Do you trust what I trust?<br>Me, myself, and I<br>Penetrate the smoke screen, I see through the selfish lie<br><br>Doesn&apos;t matter what you see<br>Or into it what you read<br>You can do it your own way<br>If it&apos;s done just how I say<br><br>Independence limited<br>Freedom of choice is made for you my friend<br>Freedom of speech is words that they will bend<br>Freedom with their exception<br><br>Do you know what I know?<br>Your money and your wealth<br>You silence just to hear yourself<br>Do you want what I want?<br>Desire not a thing<br>I hunger after independence, lengthen freedom&apos;s ring<br><br>Doesn&apos;t matter what you see<br>Or into it what you read<br>You can do it your own way<br>If it&apos;s done just how I say<br><br>Independence limited<br>Freedom of choice is made for you my friend<br>Freedom of speech is words that they will bend<br>Freedom no longer frees you<br><br>Doesn&apos;t matter what you see<br>Or into it what you read<br>You can do it your own way<br>If it&apos;s done just how I say",
          urlWikipedia:
            "http://en.wikipedia.org/wiki/Eye_of_the_Beholder_(song)",
          id_album: "5714debe25ac0d8aee36b665",
