            "http://lyrics.wikia.com/Metallica:The_Frayed_Ends_Of_Sanity",
          lyrics:
            "Oh-ee-oh, o&apos;oh-oh<br>Oh-ee-oh, o&apos;oh-oh<br>Oh-ee-oh, o&apos;oh-oh<br>Oh-ee-oh, o&apos;oh-oh<br>Oh-ee-oh, o&apos;oh-oh<br>Oh-ee-oh, o&apos;oh-oh<br><br>Never hunger, never prosper<br>I have fallen prey to failure<br>Struggle within, triggered again<br>Now the candle burns at both ends <br>Twisting under schizophrenia<br>Falling deep into dementia<br><br>Old habits reappear<br>Fighting the fear of fear<br>Growing conspiracy<br>Everyone&apos;s after me<br>Frayed ends of sanity<br>Hear them calling<br>Hear them calling me<br><br>Birth of terror, death of much more<br>I&apos;m the slave of fear, my captor<br>Never warnings, spreading its wings<br>As I wait for the horror she brings<br>Loss of interest, question, wonder<br>Waves of fear they pull me under<br><br>Old habits reappear<br>Fighting the fear of fear<br>Growing conspiracy<br>Everyone&apos;s after me<br>Frayed ends of sanity<br>Hear them calling<br>Hear them calling me<br><br>Into ruin, I am sinking<br>Hostage of this nameless feeling<br>Hell is set free, flooded, I&apos;ll be<br>Feel the undertow inside me<br>Height, Hell, time, haste, terror, tension<br>Life, death, want, waste, mass depression<br><br>Old habits reappear<br>Fighting the fear of fear<br>Growing conspiracy<br>My self is after me<br>Frayed ends of sanity<br>Hear them calling<br>Frayed ends of sanity<br>Hear them calling<br>Hear them calling me, hahahaha",
