          lyrics:
            "Dear mother, dear father<br>What is this hell you have put me through?<br>Believer, deceiver<br>Day in, day out, live my life through you<br>Pushed onto me what&apos;s wrong or right<br>Hidden from this thing that they call life<br><br>Dear mother, dear father<br>Every thought I&apos;d think you&apos;d disapprove<br>Curator, dictator<br>Always censoring my every move<br>Children are seen but are not heard<br>Tear out everything inspired<br><br>Innocence, torn from me without your shelter<br>Barred reality, I&apos;m living blindly<br><br>Dear mother, dear father<br>Time has frozen still, what&apos;s left to be<br>Hear nothing, say nothing<br>Cannot face the fact I think for me<br>No guarantee, it&apos;s life as is<br>But damn you for not giving me my chance<br><br>Dear mother, dear father<br>You clipped my wings before I learnt to fly<br>Unspoiled, unspoken<br>I&apos;ve outgrown that fucking lullaby<br>Same thing I&apos;ve always heard from you<br>Do as I say, not as I do<br><br>Innocence, torn from me without your shelter<br>Barred reality, I&apos;m living blindly<br><br>I&apos;m in Hell without you<br>Cannot cope without you too<br>Shocked at the world that I see<br>Innocent victim, please rescue me<br><br>Dear mother, dear father<br>Hidden in your world you&apos;ve made for me<br>I&apos;m seething, I&apos;m bleeding<br>Ripping wounds in me that never heal<br>Undying spite, I feel for you<br>Living out this Hell you always knew<br><br>You",
