            "Say your prayers, little one<br>Don't forget, my son<br>To include everyone<br>I tuck you in, warm within<br>Keep you free from sin<br>Till the Sandman he comes<br><br>Sleep with one eye open<br>Gripping your pillow tight<br><br>Exit light<br>Enter night<br>Take my hand<br>We're off to Never Never Land<br><br>Something's wrong, shut the light<br>Heavy thoughts tonight<br>And they aren't of Snow White<br>Dreams of war, dreams of liars<br>Dreams of dragon's fire<br>And of things that will bite<br><br>Sleep with one eye open<br>Gripping your pillow tight<br><br>Exit light<br>Enter night<br>Take my hand<br>We're off to Never Never Land<br>Yeah-heah!<br><br>Now I lay me down to sleep<br>(Now I lay me down to sleep)<br>Pray the Lord my soul to keep<br>(Pray the Lord my soul to keep)<br>If I die before I wake<br>(If I die before I wake)<br>Pray the Lord my soul to take<br>(Pray the Lord my soul to take)<br><br>Hush little baby, don't say a word<br>And never mind that noise you heard<br>It's just the beasts under your bed<br>In your closet, in your head<br><br>Exit light<br>Enter night<br>Grain of sand<br>Exit light<br>Enter night<br>Take my hand<br>We're off to Never Never Land<br><br>Boooooo!<br>Yeah, yeah!<br>Yo oh!<br><br>We're off to Never Never Land (Take my hand)<br>We're off to Never Never Land (Take my hand)<br>We're off to Never Never Land<br>We're off to Never Never Land<br>We're off to Never Never Land",
          urlWikipedia: "http://en.wikipedia.org/wiki/Enter_Sandman",
