          lyrics:
            "No more!<br>The crap rolls out your mouth again<br>Haven&apos;t changed, your brain is still gelatine<br>Little whispers circle around your head<br>Why don&apos;t you worry about yourself instead<br><br>Who are you? Where ya been? Where ya from?<br>Gossip is burning on the tip of your tongue<br>You lie so much you believe yourself<br>Judge not lest ye be judged yourself<br><br>Holier than thou<br>You are<br>Holier than thou<br>You are<br>You know not<br><br>Before you judge me take a look at you<br>Can&apos;t you find something better to do<br>Point the finger, slow to understand<br>Arrogance and ignorance go hand in hand<br><br>It&apos;s not who you are, it&apos;s who you know<br>Others lives are the basis of your own<br>Burn your bridges and built them back with wealth<br>Judge not lest ye be judged yourself<br><br>Holier than thou<br>You are<br>Holier than thou<br>You are<br>You know not<br><br>(yeah! who the hell are you?)<br><br>Holier than thou<br>You are<br>Holier than thou<br>You are<br>You know not<br>Not!",
