          recorded: ["1996", "--10-06"],
          abstract:
            '"The Unforgiven" is a power ballad by American thrash metal band Metallica. It was released as the second single from their eponymous fifth album Metallica. Though one of the slower tracks on the album, its chord progression is distinctly one of the heavier. The song deals with the theme of the struggle of the individual against the efforts of those who would subjugate him.The song has since spawned two sequels (both in name proper as well as thematically, albeit not musically), in the form of "The Unforgiven II", from the album ReLoad, and "The Unforgiven III", from the album Death Magnetic.',
