          urlSong: "http://lyrics.wikia.com/Metallica:Nothing_Else_Matters",
          lyrics:
            "So close, no matter how far<br>Couldn&apos;t be much more from the heart<br>Forever, trusting who we are<br>And nothing else matters<br><br>Never opened myself this way<br>Life is ours - We live it our way<br>All these words, I don&apos;t just say<br>And nothing else matters<br><br>Trust, I seek; and I find in you<br>Every day, for us, something new<br>Open mind for a different view<br>And nothing else matters<br><br>Never cared for what they do<br>Never cared for what they know<br>But I know<br><br>So close, no matter how far<br>It couldn&apos;t be much more from the heart<br>Forever, trusting who we are<br>And nothing else matters<br><br>Never cared for what they do<br>Never cared for what they know<br>But I know<br><br>I never opened myself this way<br>Life is ours - We live it our way<br>Hey, all these words, I don&apos;t just say<br>And nothing else matters<br><br>Trust, I seek; and I find in you<br>Every day, for us, something new<br>Open mind for a different view<br>And nothing else matters<br><br>Never cared for what they say<br>Never cared for games they play<br>Never cared for what they do<br>Never cared for what they know<br>And I know, yeah-h&apos;eah!<br><br>So close, no matter how far<br>Couldn&apos;t be much more from the heart<br>Forever, trusting who we are<br>No, nothing else matters",
