          urlSong: "http://lyrics.wikia.com/Metallica:Of_Wolf_And_Man",
          lyrics:
            "Off through the new day&apos;s mist, I run<br>Out from the new day&apos;s mist, I have come<br>I hunt... Therefore, I am<br>Harvest the land... Taking the fallen lamb<br><br>Off through the new day&apos;s mist, I run<br>Out from the new day&apos;s mist, I have come<br>We shift... Pulse through the Earth<br>Company we keep... Roam the land while you sleep<br><br>Oh... -(Shape shift)-  Nose to the wind<br>-(Shape shift)-  Feeling I&apos;ve been<br>-(Move swift)-  All senses clean<br>-(Earth&apos;s gift)-  (Back to the meaning)<br>Back to the meaning of life!<br><br>Bright is the Moon, high in starlight<br>Chill in the air, cold as steel tonight<br>We shift... Call of the wild<br>Fear in your eyes... It&apos;s later than you realized<br><br>Oh... -(Shape shift)-  Nose to the wind<br>-(Shape shift)-  Feeling I&apos;ve been<br>-(Move swift)-  All senses clean<br>-(Earth&apos;s gift)-  (Back to the meaning)<br>Back to the meaning of life!<br><br>I feel a change... Back to a better day<br>-(Shape shift)-<br>The hair stands on the back of my neck<br>-(Shape shift)-<br>Wildness is the preservation of the world...<br>So seek the wolf in thyself!<br><br>-(Shape shift)-  Nose to the wind<br>-(Shape shift)-  Feeling I&apos;ve been<br>-(Move swift)-  All senses clean<br>-(Earth&apos;s gift)-  (Back to the meaning)<br>Back to the meaning...<br>Of wolf and man!  Yeah!",
