          lyrics:
            "Pride you took<br>Pride you feel<br>Pride that you felt when you&apos;d kneel<br><br>Not the word<br>Not the love<br>Not what you thought from above<br><br>It feeds<br>It grows<br>It clouds all that you will know<br>Deceit<br>Deceive<br>Decide just what you believe<br><br>I see faith in your eyes<br>Never you hear the discouraging lies<br>I hear faith in your cries<br>Broken is the promise, betrayal<br>The healing hand held back by the deepened nail<br>Follow the god that failed!<br><br>Find your peace<br>Find your say<br>Find the smooth road on your way<br><br>Trust you gave<br>A child to save<br>Left you cold and him in grave<br><br>It feeds<br>It grows<br>It clouds all that you will know<br>Deceit<br>Deceive<br>Decide just what you believe<br><br>I see faith in your eyes<br>Never you hear the discouraging lies<br>I hear faith in your cries<br>Broken is the promise, betrayal<br>The healing hand held back by the deepened nail<br>Follow the god that failed!<br><br>I see faith in your eyes<br>Broken is the promise, betrayal<br>The healing hand held back by the deepened nail<br>Follow the god that failed!<br><br>Pride you took <br>Pride you feel<br>Pride that you felt when you&apos;d kneel<br><br>Trust you gave<br>A child to save<br>Left you cold and him in grave<br><br>I see faith in your eyes<br>Never you hear the discouraging lies<br>I hear faith in your cries<br>Broken is the promise, betrayal<br>The healing hand held back by the deepened nail<br>Follow the god that failed!<br><br>Follow the god that failed!<br>Broken is the promise<br>Betrayal, betrayal, yeah!",
