            "Open doors, so I walk inside<br>Close my eyes, find my place to hide<br>And I shake as I take it in<br>Let the show begin<br><br>Open my eyes, just to have them close again<br>Well on my way, but on my way to where I&apos;ve been<br>It swallows me as it takes me in its fog<br>I twist away as I give this world a nod<br><br>Open doors, so I walk inside<br>Close my eyes, find my place to hide<br>And I shake as I take it in<br>Let the show begin<br><br>Open my eyes, just to have them close once again<br>Don&apos;t want control as it takes me down and down and down again<br>Is that the moon or just a light that lights this dead end street?<br>Is that you there or just another demon that I meet?<br><br>The higher you are, the farther you fall<br>The longer the walk, the farther you crawl<br>My body, my temple, this temple it tilts<br>Step into the house that Jack built<br><br>The higher you are, the farther you fall<br>The longer the walk, the farther you crawl<br>My body, my temple, this temple it tilts<br>Yes, this is the house that Jack built, yeah<br><br>Open doors, as I walk inside<br>Swallow me so the pain subsides<br>And I shake as I take this in<br>Let the show begin<br><br>Let the show begin<br>Let the show begin<br><br>The higher you are, the farther you fall<br>The longer the walk, the farther you crawl<br>My body, my temple, this temple it tilts<br>Yes, this is the house that Jack built<br><br>The higher you are, the farther you fall<br>The longer the walk, the farther you crawl<br>My body, my temple, this temple it tilts<br>Yes, I am, I am, I am<br><br>Open my eyes, it swallows me<br>Is that you there? I twist away<br>Away, away, away",
