          lyrics:
            "Mama, they try and break me<br><br>The window burns to light the way back home<br>A light that warms, no matter where they&apos;ve gone<br>They&apos;re off to find the hero of the day<br>But what if they should fall by someone&apos;s wicked way<br><br>Still the window burns<br>Time so slowly turns<br>Someone there is sighing<br>Keepers of the flames<br>Do you feel your name?<br>Can&apos;t you hear your babies crying?<br><br>Mama, they try and break me<br>Still, they try and break me<br><br>Excuse me while I tend to how I feel<br>These things return to me that still seem real<br>Now deservingly this easy chair<br>But the rocking&apos;s stopped by wheels of despair<br><br>Don&apos;t want your hate<br>But the fist I&apos;ve made<br>For years can&apos;t hold or feel<br>No, I&apos;m not all me<br>So please excuse me<br>While I tend to how I feel<br><br>But now the dreams and waking screams<br>That ever last the night<br>So build a wall, behind it crawl<br>And hide until it&apos;s light<br>So can you hear your babies crying now?<br><br>Still the window burns<br>Time so slowly turns<br>And someone there is sighing<br>Keepers of the flames<br>Can&apos;t you hear your names?<br>Can&apos;t you hear your babies crying?<br><br>But now the dreams and waking screams<br>That ever last the night<br>So build a wall, behind it crawl<br>And hide until it&apos;s light<br>So can&apos;t you hear your babies crying now?<br><br>Mama, they try and break me<br>Mama, they try and break me<br>Mama, they try and break me<br>Mama, they try<br>Mama, they try<br><br>Mama, they try and break me<br>Mama, they try and break me<br>Mama, they try and break me<br>Mama, they try<br>Mama, they try",
