          lyrics:
            "Mama, she has taught me well<br>Told me when I was young<br>&quot;Son, your life&apos;s an open book<br>Don&apos;t close it &apos;fore its done<br>The brightest flame burns quickest.&quot;<br>That&apos;s what I heard her say<br>A son&apos;s heart&apos;s sewed to mother<br>But I must find my way<br><br>Let my heart go<br>Let your son grow<br>Mama, let my heart go<br>Or let this heart be still<br>Yeah, still<br><br>Rebel, my new last name<br>Wild blood in my veins<br>Apron strings around my neck<br>The mark that still remains<br>I left home at an early age<br>Of what I heard was wrong<br>I never asked forgiveness<br>But what I said is done<br><br>Let my heart go<br>Let your son grow<br>Mama, let my heart go<br>Or let this heart be still<br><br>Never I ask of you<br>But never I gave<br>But you gave me your emptiness<br>That I&apos;ll take to my grave<br><br>Never I ask of you<br>But never I gave<br>But you gave me your emptiness<br>That I&apos;ll take to my grave<br><br>So let this heart be still<br><br>Mama, now I&apos;m coming home<br>I&apos;m not all you wished of me<br>But a mother&apos;s love for her son<br>Unspoken, help me be<br>Yeah, I took your love for granted<br>And all the things you said to me, yeah, yeah<br>I need your arms to welcome me<br>But a cold stone&apos;s all I see<br><br>Let my heart go<br>Let your son grow<br>Mama, let my heart go<br>Or let this heart be still<br><br>Let my heart go<br>Mama, let my heart go<br>You never let my heart go<br>So let this heart be still<br>Oh-whoa<br><br>Never I ask of you<br>But never I gave<br>But you gave me your emptiness<br>That I&apos;ll take to my grave<br><br>Never I ask of you<br>But never I gave<br>But you gave me your emptiness<br>That I&apos;ll take to my grave<br><br>So let this heart be still",
          urlWikipedia:
            "http://en.wikipedia.org/wiki/Mama_Said_(Metallica_song)",
          id_album: "5714debe25ac0d8aee36b668",
          format: ["CD single", "Gramophone record"],
          genre: ["Country music", "Rock music"],
          producer: ["Lars Ulrich", "Bob Rock", "James Hetfield"],
          recordLabel: ["Elektra Records"],
          writer: ["Lars Ulrich", "James Hetfield"],
          recorded: ["Record Plant", "--05-01", "Sausalito, California"],
          abstract:
            "\"Mama Said\" is a power ballad by American heavy metal band Metallica from their sixth album Load, with music and lyrics by James Hetfield (music credited to Hetfield/Ulrich). The lyrics represent a man or boy who is learning to find his own way in life away from his mother. The song is directly written about Hetfield's difficult relationship with his mother, who died of cancer when he was 16 years old.The song stands out among Metallica's predominantly metal catalog; its genre-blending style incorporates country, blues, and hard rock. \"Mama Said\" begins with acoustic guitar and, during the chorus, becomes flush with a country-flavored vocal harmony and steel guitar. Toward the end the song features power chords on electric guitar.\"Mama Said\" has never been featured as a part of Metallica's live setlist. Hetfield has performed this song live, however, using a single steel-stringed acoustic guitar with no drum or bass accompaniment. He also performed it along with country singer Jessi Colter on CMT's Outlaw Concert, along with Hetfield's cover of Waylon Jennings's \"Don't You Think This Outlaw Bit's Done Got Out of Hand.\"",
