            "Fortune, fame<br>Mirror vain<br>Gone insane<br>But the memory remains<br><br>Heavy rings on fingers wave<br>Another star denies the grave<br>See the nowhere crowd cry the nowhere tears of honor<br>Like twisted vines that grow<br>Hide and swallow mansions whole<br>Dim the light of an already faded primadonna<br><br>Fortune, fame<br>Mirror vain<br>Gone insane<br>Fortune, fame<br>Mirror vain<br>Gone insane<br>But the memory remains<br><br>Heavy rings hold cigarettes<br>Up to lips that time forgets<br>While the Hollywood sun sets behind your back<br>And can&apos;t the band play on?<br>Just listen, they play my song<br>Ash to ash<br>Dust to dust<br>Fade to black<br><br>Fortune, fame<br>Mirror vain<br>Gone insane<br>Fortune, fame<br>Mirror vain<br>Gone insane<br>Dance little tin goddess<br><br>Na na na na na na na, na na na na<br>Na na na na na na na, na na na na na<br>Na na na na na na na, na na na na<br>Na na na na na na na na, na na na na na<br><br>Drift away<br>Fade away<br>Little tin goddess<br>Ash to ash<br>Dust to dust<br>Fade to black<br>Fortune, fame<br>Mirror vain<br>Gone insane<br>Fortune, fame<br>Mirror vain<br>Gone insane<br>But the memory remains<br><br>Ash to ash<br>Dust to dust<br>Fade to black<br>The memory remains, yeah<br>To this faded primadonna, yeah, yeah, yeah, hey<br><br>Dance little tin goddess, dance<br><br>Na na na na na na na, na na na na<br>Na na na na na na, na na na na na<br>Na na na na na na na, na na na na<br>Na na na na na na, na na na na na<br>Na na na na na na na<br>Na na na na<br>Na na na na na na<br>Na na na na<br>Na na na na na na na<br>Na na na na<br>Na na na na na na na<br>Na na na na na<br>Na na na na na na na<br>Na na na na<br>Na na na na na na na",
