          abstract:
            '"The Memory Remains" is a song by American heavy metal band Metallica. It was the lead single from their seventh studio album, ReLoad, released in 1997. It was first performed live in a "jam" version on July 2, 1996. The song features British singer-songwriter Marianne Faithfull performing backing vocals.The song was written by James Hetfield and Lars Ulrich. Part of the intro riff is similar to Black Sabbath\'s song "Sabbath Bloody Sabbath." It can be heard playing in the strip club Bada Bing! in the episode of The Sopranos, "The Knight in White Satin Armor." The spoken words "Say yes, at least say hello" during the outro, are a reference to "The Misfits", which is the last movie Marilyn Monroe starred in. The song was used by WWE as one of the official theme songs to WrestleMania XXVIII promoting the "End of an Era" Hell in a Cell match between Triple H and The Undertaker.',
