          lyrics:
            "I look at you, then you me<br>Hungry and thirsty, are we<br>Holding the lion&apos;s share, holding the key<br>Holding me back &apos;cause I&apos;m striving to be<br><br>Better than you (better than you)<br>Better than you (better than you)<br><br>Lock horns, I push and I strive<br>Somehow I feel more alive<br>Bury the need for it, bury the seed<br>Bury me deep when there&apos;s no will to be<br><br>Better than you (better than you)<br>Oh, better than you (better than you)<br><br>Oh, can&apos;t stop this train from rollin&apos;<br>Oh, yo, nothing brings me down<br>No, can&apos;t stop this train from rollin&apos; on and on, on<br>No, no, forever on and on<br><br>Oh, can&apos;t stop this train from rollin&apos;<br>Oh, yo, you can&apos;t take it down<br>No, never stop this locomotion on and on and on<br>No, no, you can&apos;t bring me down &apos;cause I&apos;m<br><br>Better than you (better than you)<br>Better than you (better than you)<br>Ooh, better than you (better than you)<br>Better than you (better than you)<br>Much better than you<br><br>Better than you (better than you)<br>Oh, better than you (better than you)<br>Better than you (better than you)<br>Better than... better than... better than<br>Better than you, yeah<br><br>Can&apos;t stop this train from rollin&apos;<br>Forever on and on and on and on and on<br>Forever on and on and on<br>Better than you... yeah, haha<br>Better than you",
