          urlSong: "http://lyrics.wikia.com/Metallica:Attitude",
          lyrics:
            "&quot;Suppose,&quot; I say... &quot;I&apos;m never satisfied.&quot;<br>&quot;Suppose,&quot; I say...<br>&quot;You cut some root to make the tree survive.&quot;<br><br>Just let me kill you for a while<br>Just let me kill you for a smile<br>Just let me kill you once<br>I&apos;m, oh, so bored to death!<br><br>Ooh, I hunger... I hunger... I eat!<br><br>Born into attitude<br>Asleep at the wheel<br>Throw all your bullets in the fire,<br>And run like hell<br><br>Why cure the fever?<br>What ever happened to sweat?<br><br>&quot;Suppose,&quot; I say... &quot;The vultures smile at me.&quot;<br>&quot;Suppose,&quot; I say...<br>&quot;I sent them down and they plan to pick you clean.&quot;<br><br>And satisfaction, this way, comes<br>And satisfaction, this way, comes<br>And satisfaction&apos;s here and gone,<br>Gone, yeah, gone again!<br><br>Oh, I hunger... Ooh, I hunger... I eat!<br><br>Born into attitude<br>Asleep at the wheel<br>Throw all your bullets in the fire,<br>And stand there<br><br>Born into attitude<br>Twist mother tongue<br>Throw all your bullets in the fire,<br>And run like hell<br><br>Why cure the fever?<br>What ever happened to sweat?<br><br>Just let me kill you for a while<br>Just let me kill you for a smile<br>Just let me kill you once for me<br>I&apos;m bored to death!<br><br>And satisfaction, this way, comes<br>And satisfaction, this way, comes<br>And satisfaction&apos;s here and gone,<br>Gone, gone again!<br><br>Yeah, I hunger... Ooh, I hunger... I eat!<br><br>Born into attitude<br>Asleep at the wheel<br>Throw all your bullets in the fire,<br>And stand there<br><br>Born into attitude<br>Twist mother tongue<br>Throw all your bullets in the fire,<br>And run like hell<br><br>Why cure the fever?<br>What ever happened to sweat?",
