          position: 12,
          urlSong: "http://lyrics.wikia.com/Metallica:Fixxxer",
          lyrics:
            "Yeah...<br>Dolls of voodoo all stuck with pins<br>One for each of us and our sin<br><br>So you lay us in a line<br>Push your pins, they make us humble<br>Only you can tell, in time,<br>If we fall or merely stumble<br><br>But tell me, can you heal what father&apos;s done,<br>Or fix this hole in a mother&apos;s son?<br>Can you heal the broken worlds within?<br>Can you strip away, so we may start again?<br><br>Tell me, can you heal what father&apos;s done,<br>Or cut this rope and let us run?<br>Just when all seems fine and I&apos;m pain free,<br>You jab another pin, jab another pin in me<br><br>Yeah...<br>Mirror, mirror upon thy wall<br>Break this spell or become the doll<br><br>See you sharpening the pins<br>So the holes will remind us,<br>We&apos;re just the toys in the hands of another<br>And in time the needles turn from shine to rust<br><br>But tell me, can you heal what father&apos;s done,<br>Or fix this hole in a mother&apos;s son?<br>Can you heal the broken worlds within?<br>Can you strip away, so we may start again?<br><br>Tell me, can you heal what father&apos;s done,<br>Or cut this rope and let us run?<br>Just when all seems fine and I&apos;m pain free,<br>Jab another pin, jab another pin in me...<br>Jab it... (&quot;Here come the pins&quot;)<br><br>Blood for face, sweat for dirt<br>Three X&apos;s for the stone<br>To break this curse, a ritual&apos;s due<br>I believe I&apos;m not alone<br><br>Shell of shotgun, pint of gin<br>Numb us up to shield the pins<br>Renew our faith which way we can<br>To fall in love with life again<br><br>To fall in love with life again<br>To fall in love with life again<br>To fall in love, to fall in love<br>To fall in love with life again<br><br>So tell me, can you heal what father&apos;s done,<br>Or fix this hole in a mother&apos;s son?<br>Can you heal the broken worlds within?<br>Can you strip away, so we may start again?<br><br>Yeah, tell me, can you heal what father&apos;s done,<br>Or cut this rope and let us run?<br>Just when all seems fine and I&apos;m pain free,<br>Jab another pin, jab another pin in me<br>Yeah-eh-eh-h&apos;eah<br><br>No more pins in me, yeah<br>No more, no more pins in me<br>No more, no more pins in me<br>No more, no more, no more<br>No, no, no!",
