          lyrics:
            "As I was goin&apos; over<br>The Cork and Kerry mountains<br>I saw Captain Farrell<br>And his money he was countin&apos;<br><br>I first produced my pistol<br>And then produced my rapier<br>I said, &quot;Stand and deliver.&quot;<br>Oh, &quot;Or the Devil, he may take ya.&quot;<br><br>Yeah<br><br>I took all of his money<br>And it was a pretty penny<br>I took all of his money<br>Yeah, and I brought it home to Molly<br><br>She swore that she&apos;d love me<br>No, never would she leave me<br>But the Devil take that woman<br>Yeah, for you know she tricked me easy<br><br>Mush-a-ring dum a do dum a da<br>Whack for my daddy-o<br>Whack for my daddy-o<br>There&apos;s whiskey in the jar-o<br><br>Bein&apos; drunk and weary<br>I went to Molly&apos;s chamber<br>Takin&apos; Molly with me<br>But I never knew the danger<br><br>For about six or maybe seven<br>Yeah, in walked Captain Farrell<br>I jumped up, fired my pistols<br>And I shot him with both barrels, yeah<br><br>Mush-a-ring dum a do dum a da, yeah, yeah<br>Whack for my daddy-o<br>Whack for my daddy-o <br>There&apos;s whiskey in the jar-o<br><br>Yeah, whiskey<br>Yo, whiskey<br>Whoa yeah hey yeah, wow<br>Oh-oh-oh-oh-oh ho-ooh-oh<br>Yeah, hey yeah<br><br>Now, some men like a fishin&apos; <br>And some men like the foulin&apos;<br>Some men like to hear<br>To hear the cannonball roarin&apos;<br><br>Me, I like sleepin&apos;<br>&apos;Specially in my Molly&apos;s chamber<br>But here I am in prison<br>Here I am with a ball and chain, yeah<br><br>Mush-a-ring dum a do dum a da, hey heh<br>Whack for my daddy-o<br>Whack for my daddy-o<br>There&apos;s whiskey in the jar-o, yeah<br>Whiskey in the jar-o, yeah<br><br>Mush-a-ring dum a do dum a da<br>Mush-a-ring dum a do dum a da<br>Hey-ah<br>Mush-a-ring dum a do dum a da<br>Mush-a-ring dum a do dum a da<br>Yeah-eh",
