          recorded: ["September–October 1998"],
          abstract:
            '"Whiskey in the Jar" is a well-known Irish traditional song, set in the southern mountains of Ireland, often with specific mention of counties Cork and Kerry, as well as Fenit, a village in County Kerry. The song is about a Rapparee (Highwayman), who is betrayed by his wife or lover, and is one of the most widely performed traditional Irish songs. It has been recorded by numerous professional artists since the 1950s.The song first gained wide exposure when the Irish folk band The Dubliners performed it internationally as a signature song, and recorded it on three albums in the 1960s. Building on their success, the Irish rock band Thin Lizzy hit the Irish and British pop charts with the song in the early 1970s. In 1990 The Dubliners re-recorded the song with The Pogues with a faster rocky version charting at No.4 in Ireland and No.63 in the UK. The American metal band Metallica brought it to a wider rock audience in 1998 by playing a version very similar to that of Thin Lizzy\'s, though with a heavier sound, winning a Grammy for the song in 2000 for Best Hard Rock Performance.',
