          urlSong: "http://lyrics.wikia.com/Metallica:No_Leaf_Clover",
          lyrics:
            "And it feels right this time<br>On his crash course with the big time<br>Pay no mind to the distant thunder<br>New day fills his head with wonder, boy<br><br>Says it feels right this time<br>Turned it &apos;round, and found the right line<br>&quot;Good day to be alive, sir&quot;<br>&quot;Good day to be alive,&quot; he said, yeah<br><br>Then it comes to be that the soothing light<br>At the end of your tunnel<br>Was just a freight train coming your way<br>Then it comes to be that the soothing light<br>At the end of your tunnel<br>Was just a freight train coming your way, yeah<br><br>Don&apos;t it feel right like this?<br>All the pieces fall to his wish<br>&quot;Sucker for that quick reward, boy&quot;<br>&quot;Sucker for that quick reward,&quot; they said<br><br>Then it comes to be that the soothing light<br>At the end of your tunnel<br>Is just a freight train coming your way<br>Then it comes to be that the soothing light<br>At the end of your tunnel<br>Is just a freight train coming your way, yeah<br><br>It&apos;s coming your way<br>It&apos;s coming your way, yeah<br>Yeah<br>Here it comes<br><br>Yeah, then it comes to be that the soothing light<br>At the end of your tunnel<br>Was just a freight train coming your way, yeah<br><br>Then it comes to be, yeah yeah<br>Then it comes to be, yeah yeah<br>Then it comes to be, yeah yeah<br>Then it comes to be, yeah<br><br>Then it comes to be, yeah<br>Then it comes to be, yeah<br>Then it comes to be, yeah yeah<br>Then it comes to be, yeah",
          urlWikipedia: "http://en.wikipedia.org/wiki/No_Leaf_Clover",
          id_album: "5714debe25ac0d8aee36b66b",
          format: ["CD single"],
          genre: ["Symphonic metal"],
          producer: ["Metallica", "Bob Rock"],
          recordLabel: ["Elektra Records"],
          writer: ["Lars Ulrich", "James Hetfield"],
          recorded: [
            "--04-21",
            "Berkeley Community Theatre",
            "Berkeley, California"
          ],
          abstract:
            '"No Leaf Clover" is the eighth song on the live album S&M by Metallica and the San Francisco Symphony. The song (along with "Minus Human") was one of two new pieces completed for the band\'s collaboration with the San Francisco Symphony in 1999. It begins with an intro section played by the orchestra, before entering into a clean guitar part by James Hetfield. The rest of the piece alternates between clean verses and choruses backed by heavily distorted guitars. It was the band\'s fourth No. 1 hit on the Billboard Hot Mainstream Rock Tracks chart. It stayed at No. 1 for seven consecutive weeks.  It was also featured in the trailer created by the distributor A.D. Vision for their DVD releases of the TV series Farscape.The song "No Leaf Clover" appears in the 2009 music game Guitar Hero: Metallica.',
