          lyrics:
            "Been here before<br>Been here before<br>Been here before<br>Been here before<br>Been here before<br>Been here before<br>Been here before<br><br>Been here before, couldn&apos;t say I liked it<br>Do I start writing all this down?<br>Just let me plug you into my world<br>Can&apos;t you help me be uncrazy?<br><br>Name this for me, heat the cold air<br>Take the chill off of my life<br>And if I could, I&apos;d turn my eyes<br>To look inside to see what&apos;s comin&apos;<br><br>It comes alive<br>It comes alive<br>It comes alive<br>And I die a little more<br>It comes alive<br>Each moment here I die a little more<br><br>Then the unnamed feeling<br>It comes alive\t<br>Then the unnamed feeling<br>Takes me away<br><br>Been here before<br>Been here before<br><br>I&apos;m frantic in your soothing arms <br>I cannot sleep in this down-filled world<br>I&apos;ve found safety in this loneliness<br>But I can not stand it anymore<br><br>Cross my heart hope not to die<br>Swallow evil, ride the sky<br>Lose myself in a crowded room<br>You fool, you fool, it will be here soon<br><br>It comes alive<br>It comes alive<br>It comes alive<br>And I die a little more<br>It comes alive<br>Each moment here I die a little more<br><br>Then the unnamed feeling<br>It comes alive\t<br>Then the unnamed feeling<br>Treats me this way<br>And I wait for this train<br>Toes over the line<br>And then the unnamed feeling<br>Takes me away<br><br>Then the unnamed feeling<br>It comes alive\t<br>Then the unnamed feeling<br>Treats me this way<br>And I wait for this train<br>Toes over the line<br>And then the unnamed feeling<br>Takes me away<br><br>Get the fuck out of here<br>I just wanna get the fuck away from me<br>I rage, I glaze, I hurt, I hate<br>I hate it all, why? Why? Why me?<br><br>I cannot sleep with a head like this<br>I wanna cry, I wanna scream<br>I rage, I glaze, I hurt, I hate<br>I wanna hate it all away<br><br>Then the unnamed feeling<br>It comes alive\t<br>Then the unnamed feeling<br>Treats me this way<br>And I wait for this train<br>Toes over the line<br>And then the unnamed feeling<br>Takes me away<br>Yeah, takes me away<br><br>Then the unnamed feeling<br>It comes alive<br>Then the unnamed feeling<br>Takes me away",
