          lyrics:
            "Born to push you around<br>Better just stay down<br>You pull away, he hits the flesh, you hit the ground<br>Mouths so full of lies<br>Tend to block your eyes<br>Just keep them closed, keep prayin&apos;, just keep waitin&apos;<br><br>Waiting for the one<br>The day that never comes<br>When you stand up and feel the warmth<br>But the sunshine never comes, no<br>No, the sunshine never comes<br><br>Push you &apos;cross that line<br>Just stay down this time<br>Hide in yourself, crawl in yourself, you&apos;ll have your time<br>God, I&apos;ll make them pay<br>Take it back one day<br>I&apos;ll end this day, I&apos;ll splatter color on this gray<br><br>Waiting for the one<br>The day that never comes<br>When you stand up and feel the warmth<br>But the sunshine never comes<br><br>Love is a four letter word<br>And never spoken here<br>Love is a four letter word<br>Here in this prison<br><br>I suffer this no longer<br>I&apos;ll put an end to this, I swear<br>This, I swear: The sun will shine<br>This I swear<br>This I swear<br>This I swear",
