          recorded: ["--03-12"],
          abstract:
            "\"Cyanide\" is a song by American heavy metal band Metallica, the third single taken from their ninth studio album, Death Magnetic. On September 1, 2008, it was made available for streaming on the band's official website, as well as a download (for Platinum Members only) from the Death Magnetic website Mission: Metallica. It has since been made available for purchase as a digital single in the iTunes Store.The song was played live for the first time on August 9, 2008 at Ozzfest in Dallas, Texas and was the first song from Death Magnetic to be performed live in its entirety. An audio recording of the performance is featured on the band's MySpace page. Song was also performed live on Later...With Jools Holland in 2008.",
