          lyrics:
            "How could he know this new dawn's light<br>Would change his life forever?<br>Set sail to sea but pulled off course<br>By the light of golden treasure<br><br>Was he the one causing pain<br>With his careless dreaming?<br>Been afraid, always afraid<br>Of the things he's feeling<br><br>He could just be gone<br>He would just sail on<br>He'll just sail on<br><br>How can I be lost<br>If I've got nowhere to go?<br>Search for seas of gold<br>How come it's got so cold?<br>How can I be lost<br>In remembrance I relive?<br>And how can I blame you<br>When it's me I can't forgive?<br><br>These days drift on inside a fog<br>It's thick and suffocating<br>This sinking life, outside it's hell<br>Inside intoxicating<br><br>He's run aground, like his life<br>Water much too shallow<br>Slipping fast, down with his ship<br>Fading in the shadows <br><br>Now a castaway<br>They've all gone away<br>They've gone away<br><br>How can I be lost<br>If I've got nowhere to go?<br>Searched for seas of gold<br>How come it's got so cold?<br>How can I be lost<br>In remembrance I relive?<br>And how can I blame you<br>When it's me I can't forgive?<br><br>Forgive me<br>Forgive me not<br>Forgive me<br>Forgive me not<br>Forgive me<br>Forgive me not<br>Forgive me<br>Forgive me, why can't I forgive me?<br><br>Set sail to sea but pulled off course<br>By the light of golden treasure<br>How could he know this new dawn's light<br>Would change his life forever<br><br>How can I be lost<br>If I've got nowhere to go?<br>Search for seas of gold<br>How come it's got so cold?<br>How can I be lost<br>In remembrance I relive?<br>So how can I blame you<br>When it's me I can't forgive?",
