          urlSong: "http://lyrics.wikia.com/Metallica:The_Judas_Kiss",
          lyrics:
            "When the world has turned its back<br>When the days have turned pitch black<br>When the fear abducts your tongue<br>When the fire&apos;s dead and gone<br><br>So what now?<br>Where go I?<br><br>When you think it&apos;s all said and done<br><br>When you are the ostracized<br>Selfish ridden dead goodbyes<br>Twisting of the tourniquet<br>When the pieces never fit<br><br>So what now?<br>Where go I?<br><br>When you think it&apos;s all said and done<br><br>Bow down<br>Sell your soul to me<br>I will set you free<br>Pacify your demons<br>Bow down<br>Surrender unto me<br>Submit infectiously<br>Sanctify your demons<br>Into abyss<br>You don&apos;t exist<br>Cannot resist<br>The Judas Kiss<br><br>When the storm has blacked your sky<br>Intuition crucify<br>When the ego strips your reign<br>Assassinate the living flame<br><br>So what now?<br>Where go I?<br><br>When you think it&apos;s all said and done<br><br>Venom of a life insane<br>Bites into your fragile vein<br>Internalize and decimate<br>Patronize and complicate<br><br>So what now?<br>Where go I?<br><br>When you think it&apos;s all said and done<br><br>Bow down<br>Sell your soul to me<br>I will set you free<br>Pacify your demons<br>Bow down<br>Surrender unto me<br>Submit infectiously<br>Sanctify your demons<br>Into abyss<br>You don&apos;t exist<br>Cannot resist<br>The Judas Kiss<br><br>Judas lives, recite this vow<br>I&apos;ve become your new God now<br><br>Followed you from dawn of time<br>Whispered thoughts into your mind<br>Watched your towers hit the ground<br>Lured your children never found<br>Helped your kings abuse their crown<br>In the heart of feeble man<br>Plant the seed of my own plan<br>The strong and powerful will fall<br>Find a piece of me in all.....<br><br>Inside you all,<br><br>So, bow down<br>Sell your soul to me<br>I will set you free<br>Pacify your demons<br>Bow down<br>Surrender unto me<br>Submit infectiously<br>Sanctify your demons<br>Into abyss<br>You don&apos;t exist<br>Cannot resist<br>The Judas Kiss",
