          abstract:
            'Not to be confused with the song My Apocalypse by Arch Enemy."My Apocalypse" is the forty-first single by American heavy metal band Metallica, and the second from their ninth studio album, Death Magnetic. On August 26, 2008, it was made available for streaming on the band\'s official website, as well as a download (For Platinum Members Only) from the Death Magnetic website Mission: Metallica. This was followed by its availability for purchase as a digital single in the iTunes Store. At the 2009 Grammy Awards, the single won a Grammy in the "Best Metal Performance" category. The name of the album is derived from lyrics in the song.Recently a new introduction to the song was added, Lars Ulrich said "We\'ve been enjoying playing My Apocalypse out here on the road but felt like it could use something extra. We decided that it needed a cool intro to set the mood so James wrote one."',
