          lyrics:
            "End of passion play, crumbling away<br>I&apos;m your source of self-destruction<br>Veins that pump with fear, sucking darkest clear<br>Leading on your death&apos;s construction<br><br>Taste me you will see<br>More is all you need<br>Dedicated to<br>How I&apos;m killing you<br><br>Come crawling faster<br>Obey your master<br>Your life burns faster<br>Obey your master<br>Master<br><br>Master of puppets, I&apos;m pulling your strings<br>Twisting your mind, smashing your dreams<br>Blinded by me, you can&apos;t see a thing<br>Just call my name &apos;cause I&apos;ll hear you scream<br>&quot;Master, master&quot;<br>Just call my name &apos;cause I&apos;ll hear you scream<br>&quot;Master, master&quot;<br><br>Needlework the way, never you betray<br>Life of death becoming clearer<br>Pain monopoly, ritual misery<br>Chop your breakfast on a mirror<br><br>Taste me you will see<br>More is all you need<br>Dedicated to<br>How I&apos;m killing you<br><br>Come crawling faster<br>Obey your master<br>Your life burns faster<br>Obey your master<br>Master<br><br>Master of puppets, I&apos;m pulling your strings<br>Twisting your mind, smashing your dreams<br>Blinded by me, you can&apos;t see a thing<br>Just call my name &apos;cause I&apos;ll hear you scream<br>&quot;Master, master&quot;<br>Just call my name &apos;cause I&apos;ll hear you scream<br>&quot;Master, master&quot;<br><br>Master, master<br>Master, master<br><br>Master, master<br>Where&apos;s the dreams that I&apos;ve been after?<br>Master, master<br>Promised only lies<br>Laughter, laughter<br>All I hear and see is laughter<br>Laughter, laughter<br>Laughing at my cries<br>Fix me<br><br>Hell is worth all that, natural habitat<br>Just a rhyme without a reason<br>Neverending maze, drift on numbered days<br>Now your life is out of season<br><br>I will occupy<br>I will help you die<br>I will run through you<br>Now I rule you too<br><br>Come crawling faster<br>Obey your master<br>Your life burns faster<br>Obey your master<br>Master<br><br>Master of puppets, I&apos;m pulling your strings<br>Twisting your mind, smashing your dreams<br>Blinded by me, you can&apos;t see a thing<br>Just call my name &apos;cause I&apos;ll hear you scream<br>&quot;Master, master&quot;<br>Just call my name &apos;cause I&apos;ll hear you scream<br>&quot;Master, master&quot;",
          urlWikipedia: "http://en.wikipedia.org/wiki/Master_of_Puppets_(song)",
