          lyrics:
            "My life suffocates<br>Planting seeds of hate<br>I&apos;ve loved, turned to hate<br>Trapped far beyond my fate<br>I give, you take<br>This life that I forsake<br>Been cheated of my youth<br>You turned this lie to truth<br><br>Anger, misery<br>You&apos;ll suffer unto me<br><br>Harvester of sorrow<br>Language of the mad<br>Harvester of sorrow<br><br>Pure black looking clear<br>My work is done soon here<br>Try getting back to me<br>Get back what used to be<br>Drink up, shoot in<br>Let the beatings begin<br>Distributor of pain<br>Your loss becomes my gain<br><br>Anger, misery<br>You&apos;ll suffer unto me<br><br>Harvester of sorrow<br>Language of the mad<br>Harvester of sorrow<br><br>All have said their prayers<br>Invade their nightmares<br>To see into my eyes<br>You&apos;ll find where murder lies<br>Infanticide<br><br>Harvester of sorrow<br>Language of the mad<br>Harvester of sorrow<br>Language of the mad<br>Harvester of sorrow<br>Harvester of sorrow<br>Harvester of sorrow<br>Harvester of sorrow<br>Harvester of sorrow<br>Harvester of sorrow",
          urlWikipedia: "http://en.wikipedia.org/wiki/Harvester_of_Sorrow",
          id_album: "5714debe25ac0d8aee36b66f",
          format: ["CD single", "Gramophone record"],
