          producer: ["Flemming Rasmussen"],
          recordLabel: ["Elektra Records"],
          writer: ["James Hetfield", "Lars Ulrich"],
          recorded: ["Extasy Records", "*1988 *"],
          abstract:
            '"Harvester of Sorrow" is a song by the American heavy metal band Metallica. It was released as the first single from their fourth studio album, ...And Justice for All (1988). The song debuted at a live performance prior to the release of ...And Justice for All while on the summer Monsters of Rock Tour in 1988 with Van Halen, Scorpions, Dokken and Kingdom Come. The single contained two B-sides, both of which were cover songs: "Breadfan", originally by Budgie, and "The Prince", originally by Diamond Head. There was an error in the mastering of the recording: At the end of "Breadfan", a distorted voice can be heard saying "Mommy, where\'s Fluffy?". This was actually intended to be the intro to the next track, "The Prince". However, it was separated in the wrong place. The band decided not to correct this error when the tracks were included on their 1998 Garage Inc. compilation.',
