          writer: ["Lars Ulrich", "James Hetfield"],
          recorded: ["Extasy Records"],
          abstract:
            '"One" is an anti-war song by the American heavy metal band Metallica. It was released as the third and final single from their fourth studio album, ...And Justice for All (1988). "One" was also the band\'s first Top 40 hit single, reaching number 35 on the Billboard Hot 100. The song hit number one in Finland.Metallica performed "One" for the 1988 Grammy Awards show broadcast from Los Angeles. The next year the song won a Grammy Award in the first Best Metal Performance category. The song is one of the band\'s most popular pieces and has remained a live staple since the release of the album, making this the most played song from the album ...And Justice for All.A video for the song was introduced in January 1989 on MTV. It portrays a World War I soldier who is severely wounded—blind and unable to move—returned home as a supposed vegetable case to wait helplessly for death. His only hope is to devise a way to communicate with the hospital staff. Shot in black and white by director Michael Salomon, the video\'s story is intercut with scenes taken from the 1971 anti-war film Johnny Got His Gun. Due to routinely being required to pay royalty fees to continue showing the music video, Metallica bought the rights to the film. The video was ranked No. 1 on MTV soon after its introduction.',
