          lyrics:
            "Say your prayers, little one<br>Don&apos;t forget, my son<br>To include everyone<br>I tuck you in, warm within<br>Keep you free from sin<br>Till the Sandman he comes<br><br>Sleep with one eye open<br>Gripping your pillow tight<br><br>Exit light<br>Enter night<br>Take my hand<br>We&apos;re off to Never Never Land<br><br>Something&apos;s wrong, shut the light<br>Heavy thoughts tonight<br>And they aren&apos;t of Snow White<br>Dreams of war, dreams of liars<br>Dreams of dragon&apos;s fire<br>And of things that will bite<br><br>Sleep with one eye open<br>Gripping your pillow tight<br><br>Exit light<br>Enter night<br>Take my hand<br>We&apos;re off to Never Never Land<br>Yeah-heah!<br><br>Now I lay me down to sleep<br>(<i>Now I lay me down to sleep</i>)<br>Pray the Lord my soul to keep<br>(<i>Pray the Lord my soul to keep</i>)<br>If I die before I wake<br>(<i>If I die before I wake</i>)<br>Pray the Lord my soul to take<br>(<i>Pray the Lord my soul to take</i>)<br><br>Hush little baby, don&apos;t say a word<br>And never mind that noise you heard<br>It&apos;s just the beasts under your bed<br>In your closet, in your head<br><br>Exit light<br>Enter night<br>Grain of sand<br>Exit light<br>Enter night<br>Take my hand<br>We&apos;re off to Never Never Land<br><br>Boooooo!<br>Yeah, yeah!<br>Yo oh!<br><br>We&apos;re off to Never Never Land (Take my hand)<br>We&apos;re off to Never Never Land (Take my hand)<br>We&apos;re off to Never Never Land<br>We&apos;re off to Never Never Land<br>We&apos;re off to Never Never Land",
