          lyrics:
            "Don&apos;t tread on me!<br>Said, don&apos;t tread on me!<br><br>&quot;Liberty or death!&quot;,<br>What we so proudly hail<br>Once you provoke Her:<br>Rattling of Her tail<br><br>Never begins it<br>Never, but once engaged:<br>Never surrenders, showing the fangs of rage!<br>Said, don&apos;t tread on me!<br><br>So be it!  Threaten no more!<br>To secure peace is to prepare for war<br>So be it!  Settle the score!<br>Touch me again, for the words that- <br>-you will hear ever more:  &quot;Don&apos;t tread on me!&quot;<br><br>Love it or leave it!<br>She, with the deadly bite<br>Quick as the blue tongue,<br>Forked as a lighting strike<br><br>Shining with brightness<br>Always on surveillance<br>(Eyes that never close)  Emblem of vigilance!<br>H&apos;ooh, no-no-no&apos;oh... So don&apos;t tread on me!<br><br>So be it!  Threaten no more!<br>To secure peace is to prepare for war<br>So be it!  Settle the score!<br>Touch me again, for the words that- <br>-you will hear ever more:  &quot;Don&apos;t tread on me!&quot;<br><br>So be it!  Threaten no more!<br>To secure peace is to prepare for war...<br>&quot;Liberty or death!&quot;,<br>What we so proudly hail<br>Once you provoke Her:<br>Rattling of Her tail<br><br>So be it!  Threaten no more!<br>To secure peace is to prepare for war<br>So be it!  Settle the score!<br>Touch me again, for the words that- <br>-you will hear ever more:  &quot;Don&apos;t tread on me!&quot;<br><br>*(Yes, &quot;Her&quot; is supposed to be capitalized. Do not change.)",
