          lyrics:
            "New blood joins this earth<br>And quickly he&apos;s subdued<br>Through constant pained disgrace<br>The young boy learns their rules<br><br>With time the child draws in<br>This whipping boy done wrong<br>Deprived of all his thoughts<br>The young man struggles on and on he&apos;s known<br>A vow unto his own<br>That never from this day<br>His will they&apos;ll take away<br><br>What I&apos;ve felt<br>What I&apos;ve known<br>Never shined through in what I&apos;ve shown<br>Never be<br>Never see<br>Won&apos;t see what might have been<br><br>What I&apos;ve felt<br>What I&apos;ve known<br>Never shined through in what I&apos;ve shown<br>Never free<br>Never me<br>So I dub thee unforgiven<br><br>They dedicate their lives<br>To running all of his<br>He tries to please them all<br>This bitter man he is<br>Throughout his life the same<br>He&apos;s battled constantly<br>This fight he cannot win<br>A tired man they see no longer cares<br>The old man then prepares<br>To die regretfully<br>That old man here is me<br><br>What I&apos;ve felt<br>What I&apos;ve known<br>Never shined through in what I&apos;ve shown<br>Never be<br>Never see<br>Won&apos;t see what might have been<br><br>What I&apos;ve felt<br>What I&apos;ve known<br>Never shined through in what I&apos;ve shown<br>Never free<br>Never me<br>So I dub thee unforgiven<br><br>What I&apos;ve felt<br>What I&apos;ve known<br>Never shined through in what I&apos;ve shown<br>Never be<br>Never see<br>Won&apos;t see what might have been<br><br>What I&apos;ve felt<br>What I&apos;ve known<br>Never shined through in what I&apos;ve shown<br>Never free<br>Never me<br>So I dub thee unforgiven<br><br>Never free<br>Never me<br>So I dub thee unforgiven<br><br>You labeled me<br>I&apos;ll label you<br>So I dub thee unforgiven<br><br>Never free<br>Never me<br>So I dub thee unforgiven<br><br>You labeled me<br>I&apos;ll label you<br>So I dub thee unforgiven<br><br>Never free<br>Never me<br>So I dub thee unforgiven",
