          abstract:
            '"Hero of the Day" is a power ballad by American heavy metal band Metallica from their 1996 album Load. The song was first recorded on December 13, 1995, and was the first song on Load to be recorded. The song was also Metallica\'s second single release from the album. A promotional video for the track was also filmed. It became their second consecutive number-one hit on the US Billboard Hot Mainstream Rock Tracks chart. It is one of the few Metallica songs written primarily in a major key.According to article in Kerrang around time of Load release, the demo for the song is entitled "Mouldy" due to the main riff reminding James Hetfield and Lars Ulrich of a typical Bob Mould sound.  The demo was recorded on December 10, 1994, one of the earliest demo recordings for the Load sessions (the first demo to be recorded was for Wasting My Hate on November 28, 1994).This single is notable for including four Motörhead cover songs, recorded live direct to two-track at The Plant Studios during a rehearsal for Lemmy\'s 50th Birthday Party at the Whiskey A Go-Go, Los Angeles. These b-sides were later released on the second CD of the Garage Inc. compilation in 1998.',
