          lyrics:
            "Wish I may, wish I might<br>Have this I wish tonight<br>Are you satisfied?<br>Dig for gold, dig for fame<br>You dig to make your name<br>Are you pacified?<br><br>All the wants you waste<br>All the things you&apos;ve chased<br><br>And it all crashes down<br>And you break your crown<br>And you point your finger, but there&apos;s no one around<br>Just want one thing<br>Just to play the king<br>But the castle&apos;s crumbled and you&apos;re left with just a name<br>Where&apos;s your crown, King Nothing?<br>Where&apos;s your crown?<br><br>Hard and cold, bought and sold<br>A heart as hard as gold<br>Yeah, are you satisfied?<br>Wish I might, wish I may<br>You wish your life away<br>Are you pacified?<br><br>All the wants you waste<br>All the things you&apos;ve chased<br><br>Then it all crashes down<br>And you break your crown<br>And you point your finger, but there&apos;s no one around<br>Just want one thing<br>Just to play the king<br>But the castle&apos;s crumbled and you&apos;re left with just a name<br>Where&apos;s your crown, King Nothing?<br>Yaw, nothing<br>Oh where&apos;s your crown?<br><br>Huh!<br><br>Wish I may<br>Wish I might<br>Have this wish, I wish tonight<br>I want that star<br>I want it now<br>I want it all and I don&apos;t care how<br><br>Careful what you wish<br>Careful what you say<br>Careful what you wish you may regret it<br>Careful what you wish you just might get it<br><br>Then it all crashes down<br>And you break your crown<br>And you point your finger, but there&apos;s no one around<br>Just want one thing<br>Just to play the King<br>But the castle&apos;s crumbled and you&apos;re left with just a name<br>Where&apos;s your crown, King Nothing?<br>Nothing<br><br>No, you&apos;re just nothing<br>Where&apos;s your crown King Nothing?<br>No, you&apos;re just nothing<br>Absolutely nothing<br>Off to Never-Neverland",
          urlWikipedia: "http://en.wikipedia.org/wiki/King_Nothing",
