            '"Bleeding Me" is a song by American heavy metal band Metallica from their 1996 album Load.The song was never commercially released as a single, though a promotional single was distributed to radio stations in early 1997. That year, it would reach #6 on the Mainstream Rock Charts. Both the album version and a shorter (approximately 5-6 minute) radio edit (the most notable differences from the album track being the shortening of various instrumental passages and ending the song at the conclusion of the guitar solo).The song has appeared from time to time in Metallica\'s live set since its release in 1996 including the live album S&M with the San Francisco Symphony.',
