            "Gimme fuel, gimme fire<br>Gimme that which I desire, ooh! Yeah...<br><br>Yeah, turn on... I see red<br>Adrenaline crash, and crack my head<br>Nitro-junkie, paint me dead<br>And I see red<br><br>A hundred plus, through black and white<br>War horse... full &apos;head...<br>Fuck &apos;em, man! White knuckle tight!<br>Through black and white<br><br>Ooh... on I burn<br>Fuel is pumping engines<br>Burning hard, loose and clean<br>And then I burn, turning my direction<br>Quench my thirst with gasoline<br><br>So, gimme fuel, gimme fire<br>Gimme that which I desire! Yeah!<br><br>Turn on, beyond the bone<br>Swallow future, spit out hope<br>Burn your face upon the chrome<br>Yeah-h&apos;eah, oh yeah<br><br>Take the corner, join the crash, ya<br>Headlights (head-on), headlines<br>Another junkie lives too fast<br>Yeah, lives way too fast, fast, fast<br><br>Whoa-oh... ooh... on I burn<br>Fuel is pumping engines<br>Burning hard, loose and clean<br>And then I burn, turning my direction<br>Quench my thirst with gasoline<br><br>So, gimme fuel, gimme fire<br>Gimme that which I desire! Ooh, yeah<br><br>White knuckle tight<br><br>Ooh, gimme fuel<br>Gimme fire<br>My desire<br><br>Ooh... on I burn<br>Fuel is pumping engines<br>Burning hard, loose and clean<br>And then I burn, turning my direction<br>Quench my thirst with gasoline<br><br>Gimme fuel, gimme fire<br>Gimme that which I desire! Ooh!  <br><br>On I burn",
