          recorded: ["1996"],
          abstract:
            '"Better Than You" is a song by American heavy metal band Metallica. It is the fifth track on their seventh album, ReLoad. The song was originally titled "Better". It won the 1999 Grammy Award for Best Metal Performance, it was the band\'s fourth award in that category. The song refers to the story of someone with an obsession to fight to be better than another. The band performed a jam of the song for the first time in London in 1995. The single cover features the same picture as the cover of Metallica\'s Cunning Stunts as well as on the final page of Reload.',
