          lyrics:
            "On a long and lonesome highway<br>East of Omaha<br>You listen to the engines<br>Moanin&apos; out its one song<br>You think about the woman<br>Or the girl you knew the night before<br><br>But your thoughts will soon be wandering<br>The way they always do<br>When you&apos;re riding sixteen hours<br>And there&apos;s nothing much to do<br>And you don&apos;t feel much like riding<br>You just wish the trip was through<br><br>Here I am, on the road again<br>There I am, up on the stage<br>Here I go, playing star again<br>There I go, turn the page<br><br>So you walk into this restaurant<br>Strung out from the road<br>And you feel the eyes upon you<br>As you&apos;re shaking off the cold<br>You pretend it doesn&apos;t bother you<br>But you just want to explode<br><br>Yeah, most times you can&apos;t hear &apos;em talk<br>Other times you can<br>Oh, the same old clich&#xE9;s<br>&quot;Is it woman? Is it man?&quot;<br>And you always seem outnumbered<br>You don&apos;t dare make a stand<br>Make your stand<br><br>Here I am, on the road again<br>There I am, up on the stage<br>Here I go, playing star again<br>There I go, turn the page<br><br>Oh, out there in the spotlight<br>You&apos;re a million miles away<br>Every ounce of energy<br>You try to give away<br>As the sweat pours out your body<br>Like the music that you play, yeah<br><br>Later in the evening<br>As you lie awake in bed<br>With the echoes of the amplifiers<br>Ringing in your head<br>You smoke the day&apos;s last cigarette<br>Remembering what she said<br>What she said<br><br>Hey<br><br>Yeah, and here I am, on the road again<br>There I am, up on that stage<br>Here I go, playing star again<br>There I go, turn the page<br>There I go, turn that page<br><br>There I go, yeah, yeah<br>There I go, yeah, yeah, yeah<br>There I go, yeah<br>Here I go, yeah<br>There I go, oh<br>There I go<br>And I&apos;m gone",
