          lyrics:
            "Claustrophobic<br>Crawl out of this skin<br>Heart explosive<br>Reach in, pull that pin<br><br>Fear thy name: Extermination<br>Desecrate inhale the fire<br><br>So we cross that line<br>Into the grips<br>Total eclipse<br>Suffer unto my apocalypse<br><br>Deadly vision<br>Prophecy reveal<br>Death magnetic<br>Pulling closer still<br><br>Fear thy name: Annihilation<br>Desolate inhale the fire<br><br>So we cross that line<br>Into the grips<br>Total eclipse<br>Suffer unto my apocalypse<br>My apocalypse...Go!<br><br>Crushing metal, ripping skin<br>Tossing body, mannequin<br>Spilling blood, bleeding gas<br><br>Mangle flesh, snapping spine<br>Dripping bloody valentine<br>Shatter face, spitting glass<br><br>Split apart<br>Split apart<br>Split apart<br>Spit<br>Spit it out<br><br>What makes me drift a little bit closer?<br>Dead man takes the steering wheel<br>What makes me know it&#x2019;s time to cross over?<br>Born to repeat until I feel<br><br>See through the skin, bones they all rattle<br>Future and past, they disagree<br>Flesh falls away, bones they all shatter<br>I start to see the end. In. Me.<br>See the end in me<br><br>Claustrophobic<br>Climb out of this skin<br>Heart explosive<br>Reach in, pull that pin<br><br>Violate, annihilate<br>All wounds unto my eyes<br>Obliterate, exterminate<br>As life itself denied<br><br>Fear thy name as hell awakens<br>Destiny inhale the fire<br><br>But we&#x2019;ve crossed that line<br>Into the grips<br>Total eclipse<br>Suffer unto my apocalypse<br><br>Tyrant awaken my apocalypse<br>Demon awaken my apocalypse<br>Heaven awaken my apocalypse<br>Suffer forever my apocalypse",
