          lyrics:
            "Sleep and dream of this<br>Death Angel&apos;s kiss<br>Brings final bliss<br>Completely<br><br>Empty, they say<br>&quot;Death, won&apos;t you let me stay?&quot;<br>Empty, they say<br>&quot;Death, hear me call your name!&quot;<br>Ooh, call your name<br><br>Suicide, I&apos;ve already died<br>You&apos;re just the funeral I&apos;ve been waiting for<br>Cyanide, living dead inside<br>Break this empty shell forevermore<br><br>Wait, wait patiently<br>Your death-black wings<br>Unfolding sleep<br>Spreading o&apos;er me<br><br>Empty, they say<br>&quot;Death, won&apos;t you let me stay?&quot;<br>Empty, they say<br>&quot;Death, hear me call your name!&quot;<br>Ooh, call your name<br><br>Suicide, I&apos;ve already died<br>You&apos;re just the funeral I&apos;ve been waiting for<br>Cyanide, living dead inside<br>Break this empty shell forevermore<br><br>Say, is that rain or are they tears<br>That stained your concrete face for years?<br>The crying, weeping, shedding strife<br>Year after year, life after life<br><br>An air of freshly broken ground<br>A concrete angel laid right down<br>Upon the grave which swallows fast<br>It&apos;s peace at last, oh, peace at last<br><br>Ooh, empty, they say<br>&quot;Death, won&apos;t you let me stay?&quot;<br>Empty, they say<br>&quot;Death, hear me call your name!&quot;<br>Call your name<br><br>Suicide, I&apos;ve already died<br>You&apos;re just the funeral I&apos;ve been waiting for<br>Cyanide, living dead inside<br>Break this empty shell forevermore<br><br>Forevermore<br>Forevermore<br>It&apos;s just the funeral I&apos;ve been waiting for",
