          position: 39,
          urlSong: "http://lyrics.wikia.com/Metallica:All_Nightmare_Long",
          lyrics:
            "(&quot;One, two!&quot;)<br><br>&quot;Luck runs out...&quot;<br>Crawl from the wreckage one more time<br>Horrific memory twists the mind<br>Dark, rutted, cold and hard to turn<br>Path of destruction - Feel it burn<br><br>Still life... Incarnation<br>Still life... Infamy<br>Hallucination, heresy... Still you run<br>What&apos;s to come?  What&apos;s to be?<br><br>&apos;Cause we hunt you down without mercy<br>Hunt you down, all nightmare long<br>Feel us breathe upon your face<br>Feel us shift - Every move, we trace<br>Hunt you down without mercy<br>Hunt you down, all nightmare long<br>Luck runs out...<br>You crawl back in, but your luck runs out<br><br>(&quot;One, two!&quot;)<br><br>&quot;Luck runs out...&quot;<br>The light that is not light is here,<br>To flush you out with your own fear<br>You hide, you hide, but will be found<br>Release your grip without a sound<br><br>Still life... Immolation<br>Still life... Infamy<br>Hallucination, heresy... Still you run<br>What&apos;s to come?  What&apos;s to be?<br><br>&apos;Cause we hunt you down without mercy<br>Hunt you down, all nightmare long<br>Feel us breathe upon your face<br>Feel us shift - Every move, we trace<br>Hunt you down without mercy<br>Hunt you down, all nightmare long<br>Luck runs out...<br>You crawl back in, but your luck runs out<br><br>(&quot;One, two, three, four!&quot;)<br><br>Then you crawl back in,<br>Into your obsession... Never to return<br>This is your confession<br><br>Hunt you down without mercy<br>Hunt you down, all nightmare long<br>Feel us breathe upon your face<br>Feel us shift - Every move, we trace<br>Hunt you down without mercy<br>Hunt you down, all nightmare long<br>Luck runs out...<br>You crawl back in, but your luck runs out<br>Luck runs out!",
