          lyrics:
            "You rise, you fall, you&apos;re down then you rise again<br>What don&apos;t kill ya&apos; make you more strong<br>You rise, you fall, you&apos;re down then you rise again<br>What don&apos;t kill ya&apos; make you more strong<br><br>Rise, fall down, rise again<br>What don&apos;t kill ya&apos; make you more strong<br>Rise, fall down, rise again<br>What don&apos;t kill ya&apos; make you more strong<br><br>Through black days... Through black nights...<br>Through pitch black insights<br><br>Breaking your teeth on the hard life a&apos; comin&apos;<br>(Show your scars!)<br>Cutting your feet on the hard earth a&apos; runnin&apos;<br>(Show your scars!)<br>Breaking your life... Broken, beat and scarred!<br>But we die hard!<br><br>The dawn, the death, the fight to the final breath<br>What don&apos;t kill ya&apos; make you more strong<br>The dawn, the death, the fight to the final breath<br>What don&apos;t kill ya&apos; make you more strong<br><br>Dawn, death, fight final breath<br>What don&apos;t kill ya&apos; make you more strong<br>Dawn, death, fight final breath<br>What don&apos;t kill ya&apos; make you more strong<br><br>They scratch me... They scrape me...<br>They cut and rape me!<br><br>Breaking your teeth on the hard life a&apos; comin&apos;<br>(Show your scars!)<br>Cutting your feet on the hard earth a&apos; runnin&apos;<br>(Show your scars!)<br>Breaking your life... Broken, beat and scarred!<br>But we die hard!<br><br>Breaking your teeth on the hard life a&apos; comin&apos;<br>(Show your scars!)<br>Cutting your feet on the hard earth a&apos; runnin&apos;<br>(Show your scars!)<br>Braiding your soul in a hard luck story<br>(Show your scars!)<br>Spilling your blood in a hot sun&apos;s foray<br>(Show your scars!)<br>Breaking your life... Broken, beat and scarred!<br>We die hard!<br>We die hard!<br>We die hard!",
