          abstract:
            '"Broken, Beat & Scarred" is the forty-fifth single by American heavy metal band Metallica, and the sixth from their ninth studio album, Death Magnetic, released on April 3, 2009.James Hetfield and Lars Ulrich argued at length over the title of this song. Hetfield said that he did not like the title, but Ulrich was "very adamant" that it should be called "Broken, Beat & Scarred."On March 19, Metallica\'s website announced "Broken, Beat & Scarred" as the next single from the album. The single was released in two formats - a digi-collectors edition and a maxi single.',
