            "Hey, hey, hey<br>Here I go now<br>Here I go into new days<br>Hey, hey, hey<br>Here I go now<br>Here I go into new days<br><br>I&apos;m pain, I&apos;m hope, I&apos;m suffer<br>Yeah hey hey hey hey hey<br>Here I go into new days<br><br>Hey, hey, hey<br>Ain&apos;t no mercy<br>Ain&apos;t no mercy there for me<br>Hey, hey, hey<br>Ain&apos;t no mercy<br>Ain&apos;t no mercy there for me<br><br>I&apos;m pain, I&apos;m hope, I&apos;m suffer<br>Yeah, hey, hey<br>Ain&apos;t no mercy<br>Ain&apos;t no mercy there for me<br><br>Do you bury me when I&apos;m gone?<br>Do you teach me while I&apos;m here?<br>Just as soon as I belong<br>Then it&apos;s time I disappear<br><br>Hey, hey, hey<br>And I went<br>And I went on down that road<br>Hey, hey, hey<br>And I went on<br>And I went on down that road<br><br>I&apos;m pain, I&apos;m hope, I&apos;m suffer<br>Hey, hey, hey, yeah<br>And I went on<br>And I went on down that road<br><br>Do you bury me when I&apos;m gone?<br>Do you teach me while I&apos;m here?<br>Just as soon as I belong<br>Then it&apos;s time I disappear<br><br>Do you bury me when I&apos;m gone?<br>Do you teach me while I&apos;m here?<br>Just as soon as I belong<br>Then it&apos;s time I disappear<br><br><i>I&apos;m gone<br>I&apos;m gone<br>I&apos;m gone<br>Oh yeah, I&apos;m gone<br>I&apos;m gone<br>I&apos;m gone baby<br>I&apos;m gone<br>I&apos;m gone</i><br><br>Do you bury me when I&apos;m gone?<br>Do you teach me while I&apos;m here?<br>Just as soon as I belong<br>Then it&apos;s time I disappear<br><br>Do you bury me when I&apos;m gone?<br>Do you teach me while I&apos;m here?<br>Just as soon as I belong<br>Then it&apos;s time I disappear",
