          writer: ["Lars Ulrich", "James Hetfield"],
          recorded: ["2000"],
          abstract:
            '"I Disappear" is a song by American heavy metal band Metallica. The song was recorded as a contribution to the Mission: Impossible II soundtrack and reached #1 on the Billboard Hot Mainstream Rock Tracks for seven (non-consecutive) weeks in summer 2000. It does not appear on any of Metallica\'s studio albums, making it the band\'s only standalone single. The song, which won a 2000 Metal Edge Readers\' Choice Award for "Song of the Year From a Movie Soundtrack," is the last Metallica studio recording to feature bassist Jason Newsted.The discovery of an unfinished version of the song in early 2000 on the peer-to-peer file sharing network Napster helped bring the illegal sharing of mp3 files to the spotlight, leading to the lawsuits that eventually brought down the original incarnation of Napster.',
