          _id: "5714dedb25ac0d8aee4ad8d0",
          position: 9,
          urlSong: "http://lyrics.wikia.com/Metallica:One_%28Video_Version%29",
          lyrics:
            "The cerebrum has suffered massive and irreparable damage.<br>He&apos;ll never know what has happened to him.<br>If I had not been sure of this, I would not have permitted him to live.<br>Where am I?<br>Father?! <br>What happened? <br>I need help!<br>What is democracy? <br>What is democracy?<br>It&apos;s got something to do with young men killing each other, I think.<br>When it&apos;s comes my turn, will you want me to go?<br>For democracy, any man would give his only begotten son.<br><br>It is impossible for this decerebrated individual to experience pain, pleasure, memory, dreams or thought of any kind.<br>This young man will be as unfeeling, or as unthinking as the dead, until the day he joins them.<br><br>I don&apos;t know whether I&apos;m alive and dreaming or dead and remembering.<br>How can you tell what&apos;s a dream and what&apos;s real <br>When you can&apos;t even tell when you&apos;re awake and when you&apos;re asleep? <br><br>Where am I?<br><br>I can&apos;t remember anything<br>Can&apos;t tell if this is true or dream<br>Deep down inside I feel to scream<br>This terrible silence stops me<br><br>Now that the war is through with me<br>I&apos;m waking up, I cannot see<br>That there&apos;s not much left of me<br>Nothing is real but pain now<br><br>Hold my breath as I wish for death<br>Oh please God, wake me<br><br>They kept my head and chopped off everything.<br>Oh God, please make them hear me.<br>They won&apos;t listen, they won&apos;t hear me.<br>They got to wake me up; I&apos;ll be like this for years!<br>Hear me...<br><br>Back in the womb it&apos;s much too real<br>In pumps life that I must feel<br>But can&apos;t look forward to reveal<br>Look to the time when I&apos;ll live<br><br>Fed through the tube that sticks in me<br>Just like a wartime novelty<br>Tied to machines that make me be<br>Cut this life off from me<br><br>Hold my breath as I wish for death<br>Oh please God, wake me<br><br>It&apos;s like a piece of meat that keeps on living.<br>It won&apos;t always be like this, will it?!<br><br>I can&apos;t live like this! <br>I-I can&apos;t! <br>Please no! <br>I can&apos;t! I can&apos;t! <br>Help me, help me, help me! <br>Mother where are ya? <br>Mommy, mother, I&apos;m having a nightmare and I can&apos;t wake up!<br><br>Now the world is gone I&apos;m just one<br>Oh God help me <br><br>Hold my breath as I wish for death<br>Oh please God, help me<br><br>Me lying here like, like some freak in a carnival show...<br>Here is the armless,<br>Legless,<br>Wonder of the twentieth century!<br><br>Death has a dignity of its own....<br>Father! <br>I need help!<br>I&apos;m in terrible trouble and I need help!<br>Don&apos;t you remember when you were little?<br>How and you and Bill Harper use to string a wire between the two houses <br>So you could telegraph to each other?<br>You&apos;ll remember the Morse code.<br><br>Darkness <br>Imprisoning me<br>All that I see<br>Absolute horror<br>I cannot live<br>I cannot die<br>Trapped in myself<br>Body my holding cell<br><br>It&apos;s Morse code.<br>For what? <br>S.O.S. <br>Help.<br><br>Landmine <br>Has taken my sight<br>Taken my speech<br>Taken my hearing<br>Taken my arms<br>Taken my legs<br>Taken my soul<br>Left me with life in Hell<br><br>What&apos;s he saying? <br>Said kill me. <br>Over and over again... <br>Kill me!<br>Oh god, please make them hear me.<br>Don&apos;t you have any message for him, Padre?<br>He&apos;s the product of your profession,<br>Not mine.<br><br>Kill me.<br>I&apos;m asking you to kill me.<br><br>Thank you....<br><br>Save me please!<br>Father?<br><br>Each man faces death by himself.<br>Alone.<br>Good-bye father.<br><br>Inside me I&apos;m screaming nobody pays any attention.<br>If I had arms, I could kill myself.<br>If I had legs, I could run away.<br>If I had a voice, I could talk and be some kind of company for myself.<br>How do I know they&apos;ll kill me?<br>I could yell for help, but nobody&apos;d help me.<br>I just got to do some kind of, see how I can go on like this....<br><br>S.O.S. help me...<br>S.O.S. help me...<br><br>Keep the home fires burning<br>While our hearts are yearning",
